  const onSearch = () => {
    if (updatedToDateError !== "") {
      return;
    }
    if (dateRangeError !== "") {
      return;
    }
    let search = "";
    if (searchObj.roles) {
      // @ts-ignore :will do it later
      search = `${search}name:lk=${searchObj.roles}&`;
    }
    if (searchObj.code) {
      search = `${search}code:lk=${searchObj.code}&`;
    }
    if (searchObj.description) {
      search = `${search}description:lk=${searchObj.description}&`;
    }
    if (searchObj.createdFrom) {
      console.log(searchObj.createdFrom);
      search = `${search}createdAt:gt=${convertDateToISO(searchObj.createdFrom)}&`;
    }
    if (searchObj.createdTo) {
      search = `${search}createdAt:lt=${convertDateToISO(searchObj.createdTo)}&`;
    }
    if (searchObj.updatedFrom) {
      search = `${search}updatedAt:gt=${convertDateToISO(searchObj.updatedFrom)}&`;
    }
    if (searchObj.updatedTo) {
      search = `${search}updatedAt:lt=${convertDateToISO(searchObj.updatedTo)}&`;
    }
    if (searchObj.isActive) {
      let isActiveBool = true;
      if (searchObj.isActive === "YES") {
        isActiveBool = true;
      } else if (searchObj.isActive === "NO") {
        isActiveBool = false;
      }
      search = `${search}isActive:lk=${isActiveBool}&`;
    }
    if (searchObj.createdBy?.id) {
      search = `${search}&createdBy=${searchObj.createdBy?.id}&`;
    }
    if (searchObj.updatedBy?.id) {
      search = `${search}&updatedBy=${searchObj.updatedBy?.id}&`;
    }
    setDataTable({
      ...dataTable,
      search,
    });
  };

